@import '../header/header.css';
.span {
    padding-top: 10em;
}

.backButton {
    margin: 1rem;
    border: 1px solid white;
    width: fit-content;
    padding: 0.1rem;
    cursor: pointer;
    background-color: inherit;
}

.backButton > * {
    padding:0.5rem;
    text-decoration: none;
}

.backButton:hover > * {
    color: var(--brand-clr);
}

.backButton:hover {
    border: 1px solid var(--brand-clr);
}