@import '../header/header.css';
@import url('https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@300;400;900&family=Unbounded:wght@200;300;400;500;600;700;800;900&display=swap');

h2 {
    font-family: 'Unbounded', cursive;
    font-size: 2.5ch;
}

main {
    padding: 2rem 1rem 1rem 1rem;
    background-color: var(--main-clr);
    margin-top: -2rem;
    position: relative;
    border-radius: 13px;
    color: var(--main-font);
}

a {
    color: var(--main-font);
}

a:hover {
    color: var(--brand-clr);
}

.link {
    text-indent: 1rem;
}